import { Button } from 'antd';
import React from 'react';
import './Popup.css';

const Popup = () => {
  return (
    <div className="App">
      <div style={{ display: 'flex' }}>
        <img
          src={chrome.runtime.getURL('src/assets/img/logo.png')}
          className="App-logo"
          style={{ marginRight: '1rem' }}
          height={30}
          alt="logo"
        />

        <span style={{ fontWeight: '500', fontSize: '20px' }}>
          Linkedin Optimization
        </span>
      </div>
      <div style={{ margin: '1rem' }}>
        <div style={{ fontSize: '16px', marginBottom: '0.5rem' }}>
          <b>To get started</b>
        </div>
        <ol style={{ position: 'relative', left: '-1.5rem' }}>
          <li>Go to your personal LinkedIn Profile page. </li>
          <li>
            Look for the floating Careerflow icon on the right side of your
            screen.
          </li>
          <li>Click the floating icon to open the optimization panel.</li>
        </ol>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <Button
            type="primary"
            style={{ marginTop: '0.5rem' }}
            className="primary-btn"
            onClick={async () => {
              window.open('https://linkedin.com/in/');
            }}
          >
            Open Linkedin Profile
          </Button>
        </div>
      </div>
    </div>
  );
};

export default Popup;
